import "./index.css";

export const NavBar = (props) => {
  return (
    <div className="nav-bar">
      <div className="link-list">
        {props.items.map((item, index) => (
          <div
            className={index === props.selected ? "link selected" : "link"}
            key={item}
            onClick={() => props.onChange(index)}
          >
            {item}
          </div>
        ))}
      </div>
    </div>
  );
};
