import { NavBar, NavBarContainer, PageContainer } from "../../containers";
import { CareerSubPage, EducationSubPage, HomeSubPage, ProjectsSubPage, SkillsSubPage } from "../subpages";
import {ParticleBackground} from "../../components"
import React, { useState } from "react";
import classNames from "classnames";
import "./index.css"

const pages = [HomeSubPage, CareerSubPage, EducationSubPage, ProjectsSubPage, SkillsSubPage];
export const Home = (props) => {
    const [page, setPage] = useState(2);
    const onPage = (newPage) => {
        setPage(newPage);
    }
    return (
        <div className = "particle-container">
            <ParticleBackground></ParticleBackground>
            <NavBar selected={page} items={["Home", "Career", "Education", "Projects", "Skills"]} onChange={onPage}/>
            <div className="home">
                {pages.map((Page, index) => 
                <PageContainer visible={classNames(
                    {
                        "visible": index===page,
                        "invisible-left": index < page,
                        "invisible-right":index > page,
                    }
                )} key={index}><Page></Page>
                </PageContainer>)}
            </div>
        </div>
    );
}