import { useState } from "react";
import "./index.css";
import classNames from "classnames";

export const PageContainer = (props) => {
  const [lastVisiblity, setLastVisiblity] = useState(props.visible);
  if (lastVisiblity !== props.visible) {
    setLastVisiblity(props.visible);
  }
  return (
    <div className="page-container">
      <div
        className={classNames("page-sub-container", props.visible)}
        style={
          (lastVisiblity === "invisible-left" &&
            props.visible === "invisible-right") ||
          (lastVisiblity === "invisible-right" &&
            props.visible === "invisible-left")
            ? {
                transition: "0s",
              }
            : undefined
        }
      >
        <div className="page-content">{props.children}</div>
      </div>
    </div>
  );
};
